import EventType from "../../../Tool/EventType";
import UIForm from "../form/UIForm";
import adItem3 from "./adItem3";
import ExportTool_Scroller from "./ExportVIews/ExportTool_Scroller";

/**主界面
 * 实现部分竖屏游戏通用的主界面元素
 */
export  abstract class HomeFormFrame extends UIForm {
  /**剔除顶部刘海部分和底部导出广告部分剩下的中间区域 */
  protected centerZone: Laya.Sprite;
  /**关卡数字对象*/
  private level: Laya.FontClip;
  /**关卡数字占位0的数量
   * @example levelShowZero = "000" => "第001关"*/
  protected levelShowZero: string = "000";
  /** 皮肤按钮对象*/
  protected skinBtn: Laya.Button;
  /**开始游戏按钮对象 */
  private startBtn: Laya.Button;
  /**浮动广告 */
  private adList: adItem3[] = [];

  private adBaseList: ExportTool_Scroller;
  public onAwake() {
    super.onAwake();
    this.adBaseList = this.owner.getChildByName("AdList").addComponent(ExportTool_Scroller);
    this.adBaseList.scrollX = true;
    // Lite.event.sendEventImmediately(EventType.SHOW_AD, [ADType.BOTTOM]);
    this.centerZone = this.owner.getChildByName("CenterZone") as Laya.Sprite;
    this.level = this.centerZone.getChildByName("LevelNum").getChildByName("LevelFont") as Laya.FontClip;
    this.skinBtn = this.centerZone.getChildByName("SkinBtn") as Laya.Button;
    this.startBtn = this.centerZone.getChildByName("StartBtn") as Laya.Button;
    this.skinBtn.on(Laya.Event.CLICK, this, this.onSkinBtn);
    this.startBtn.on(Laya.Event.CLICK, this, () => {
      this.onStartBtn();
      moosnow.event.sendEventImmediately(EventType.GAME_STATE_START);
    });
    var list = this.owner.getChildByName("adListL") as Laya.List;
    for (let q = 0; q < list.cells.length; q++) {
      this.adList.push(list.cells[q].getComponent(adItem3));
    }
    var list2 = this.owner.getChildByName("adListR") as Laya.List;
    for (let q = 0; q < list2.cells.length; q++) {
      this.adList.push(list2.cells[q].getComponent(adItem3));
    }
  }
  public onShow() {
    this.changeExportAd();
    this.adBaseList.Act = true;
    Laya.timer.loop(5000, this, this.changeExportAd);
    this.levelNum = Lite.data.getVirtualLevel();
  }
  /**换一套主页浮动广告 */
  private changeExportAd() {
    let tmpadarr = Lite.config.getAd();
    let w = 0;
    for (let q = 0; q < this.adList.length; q++, w++) {
      if (w >= this.adList.length) {
        w = 0;
      }
      let counter = q;
      let counter2 = w;
      this.adList[q].scaleShake(this, () => {
        this.adList[counter].init(tmpadarr[counter2]);
      });
    }
  }
  public onHide() {
    this.adBaseList.Act = false;
    Laya.timer.clearAll(this);
  }
  /**当按下皮肤商店按钮 */
  protected abstract onSkinBtn();
  /**当按下开始游戏按钮 */
  protected abstract onStartBtn();
  /**设置关卡数字 */
  public set levelNum(innerNum) {
    let tmpstr: string = innerNum.toString();
    let length = tmpstr.length.valueOf();
    if (length < this.levelShowZero.length) {
      let outstr = "";
      for (let q = this.levelShowZero.length - 1; q >= length; q--) {
        outstr += "0";
      }
      outstr += tmpstr;
      this.level.value = outstr;
    } else {
      this.level.value = tmpstr;
    }
  }
}
