import BaseModule from "./BaseModule";
import EventType from "./EventType";

export default class GameDataCenter extends BaseModule {
  constructor() {
    super();
    this.initNewData();
  }

  private CURRENT_NEW_USER: string = "CURRENT_NEW_USER";
  private IS_UPDATE: string = "IS_UPDATE";
  private MUSIC_SWITCH: string = "MUSIC_SWITCH";
  private SOUND_SWITCH: string = "SOUND_SWITCH";
  private VIBRATE_SWITCH: string = "VIBRATE_SWITCH";
  private CURRENT_LEVEL: string = "CURRENT_LEVEL";
  private VIRTUAL_LEVEL: string = "VIRTUAL_LEVEL";
  private TOKEN: string = "token";
  private OPEN_ID: string = "OPEN_ID";
  private CHANNEL_ID: string = "CHANNEL_ID";
  private ATTR: string = "ATTR";
  private COIN: string = "COIN";
  private KEY: string = "KEY";
  private DIAMOND: string = "DIAMOND";
  private LAST_COIN_REWARD: string = "LAST_COIN_REWARD";
  private SIGN_NUM: string = "SIGN_NUM";
  private SIGN_DATE: string = "SIGN_DATE";
  private SIGN_VIDEO_DATE: string = "SIGN_VIDEO_DATE";
  private SPEED_UP_LEVEL: string = "SPEED_UP_LEVEL";

  private CURRENT_SKIN_0: string = "CURRENT_SKIN_0";
  private CURRENT_SKIN_1: string = "CURRENT_SKIN_1";
  private HAS_SKINS: string = "HAS_SKINS";
  private LOCK_SKIN: string = "LOCK_SKIN";
  private UNLOCKED_SKINS: string = "UNLOCKED_SKINS";
  private CURRENT_SKILL: string = "CURRENT_SKILL";
  private GAME_OUT_TIME: string = "GAME_OUT_TIME";
  private SHOP_SUMMARY: string = "SHOP_SUMMARY";
  private RECORD_COIN: string = "RECORD_COIN";
  private RECORD_KEY: string = "RECORD_KEY";

  private GUN_SKIN: string = "GUN_SKIN";
  private HELMET_SKIN: string = "HELMET_SKIN";
  private SHIELD_SKIN: string = "SHIELD_SKIN";
  private CUR_GUN_SKIN: string = "CUR_GUN_SKIN";
  private CUR_HELMET_SKIN: string = "CUR_HELMET_SKIN";
  private CUR_SHIELD_SKIN: string = "CUR_SHIELD_SKIN";
  private CUR_GUN_VALUE: string = "CUR_GUN_VALUE";
  private CUR_HELMET_VALUE: string = "CUR_HELMET_VALUE";
  private CUR_SHIELD_VALUE: string = "CUR_SHIELD_VALUE";

  private vibrate_switch = null;
  private sound_switch = null;
  //是否新手
  getIsNew() {
    return moosnow.setting.getBool(this.CURRENT_NEW_USER, true);
  }
  setIsNew(on: boolean) {
    moosnow.setting.setBool(this.CURRENT_NEW_USER, on);
  }
  //获取token
  getToken() {
    return moosnow.setting.getString(this.TOKEN, "");
  }
  setToken(v) {
    moosnow.setting.setValue(this.TOKEN, v);
  }
  //channel_id
  getChannelId() {
    return moosnow.setting.getInt(this.CHANNEL_ID, 0);
  }
  setChannelId(v) {
    moosnow.setting.setValue(this.CHANNEL_ID, v);
  }
  //获取openid
  getOpenid() {
    return moosnow.setting.getString(this.OPEN_ID, "");
  }
  setOpenid(v) {
    moosnow.setting.setValue(this.OPEN_ID, v);
  }

  //振动
  getVibrateSetting(): boolean {
    if (this.vibrate_switch == null) {
      this.vibrate_switch = moosnow.setting.getBool(this.VIBRATE_SWITCH, true);
    }
    return this.vibrate_switch;
  }
  setVibrateSetting(on: boolean) {
    moosnow.setting.setBool(this.VIBRATE_SWITCH, on);
    this.vibrate_switch = on;
    moosnow.event.sendEventImmediately(moosnow.PLATFORM_EVENT.VIBRATESWITCH_CHANGED, on);
  }

  //声音
  getSoundSetting(): boolean {
    if (this.sound_switch == null) {
      this.sound_switch = moosnow.setting.getBool(this.SOUND_SWITCH, true);
    }
    return this.sound_switch;
  }
  setSoundSetting(on: boolean) {
    moosnow.setting.setBool(this.SOUND_SWITCH, on);
    this.sound_switch = on;
    moosnow.event.sendEventImmediately(moosnow.PLATFORM_EVENT.VIBRATESWITCH_CHANGED, on);
  }
  /**
   * 转换速度
   */
  convertSpeedByDelta(speed, delta) {
    if (delta > 167) delta = 16.67;
    return speed * (delta / 1000);
  }

  //新手初始化
  initNewData() {
    if (this.getToken()) return;
    console.log("initNewDatainitNewDatainitNewData");
    //关卡初始化
    this.setCurrentLevel(1);
    this.setVirtualLevel(1);
    this.setToken(moosnow.Common.generateUUID());
    this.setSoundSetting(false);
    this.setVibrateSetting(false);
  }
  getCurrentLevelStr() {
    let lv = this.getCurrentLevel();
    return lv.toString();
  }
  //当前关卡
  getCurrentLevel(): number {
    if (this.mLevel == 0) {
      this.mLevel = moosnow.setting.getInt(this.CURRENT_LEVEL, 1);
    }
    return this.mLevel;
  }
  setCurrentLevel(level: number) {
    this.mLevel = level;
    moosnow.setting.setValue(this.CURRENT_LEVEL, this.mLevel);
    moosnow.event.sendEventImmediately(EventType.LEVEL_CHANGED, this.mLevel);
  }
  private mLevel: number = 0;
  addCurrentLevel(value: number = 1) {
    let curLevel = this.getCurrentLevel();
    curLevel++;
    this.setCurrentLevel(curLevel);
  }
  /**
   * 虚拟关卡
   */
  private mVirtualLevel: number = 0;
  public getVirtualLevel() {
    if (this.mVirtualLevel == 0) this.mVirtualLevel = moosnow.setting.getInt(this.VIRTUAL_LEVEL, 1);
    return this.mVirtualLevel;
  }
  public addVirtualLevel(v: number) {
    this.mVirtualLevel += v;
    this.saveVirtualLevel();
  }
  public setVirtualLevel(v: number) {
    this.mVirtualLevel = v;
    this.saveVirtualLevel();
  }
  public saveVirtualLevel() {
    moosnow.setting.setValue(this.VIRTUAL_LEVEL, this.mVirtualLevel);
  }

  private mCoin: number = 0;
  /***********
   * 金币
   */
  public getCoin() {
    if (this.mCoin == 0) this.mCoin = moosnow.setting.getInt(this.COIN, 0);
    return this.mCoin;
  }
  public subCoin(v: number) {
    this.mCoin -= v;
    this.saveCoin();
  }
  public addCoin(v: number) {
    this.mCoin += v;
    this.saveCoin();
  }
  public setCoin(v: number) {
    this.mCoin = v;
    this.saveCoin();
  }
  public saveCoin() {
    moosnow.setting.setValue(this.COIN, this.mCoin);
    moosnow.event.sendEventImmediately(moosnow.PLATFORM_EVENT.COIN_CHANGED, this.mCoin);
  }
  /***********
   * 速度级别
   */
  private mSpeedLevel = 0;
  public getSpeedLevel() {
    if (this.mSpeedLevel == 0) this.mSpeedLevel = moosnow.setting.getInt(this.SPEED_UP_LEVEL, 0);
    return this.mSpeedLevel;
  }
  public addSpeedLevel(v: number = 1) {
    if (this.mSpeedLevel >= 125) {
      return false;
    }
    this.mSpeedLevel += v;
    this.mSpeedLevel = this.mSpeedLevel > 125 ? 125 : this.mSpeedLevel;
    this.saveSpeedLevel();
    return true;
  }
  public setSpeedLevel(v: number) {
    this.mSpeedLevel = v;
    this.saveSpeedLevel();
  }
  public saveSpeedLevel() {
    moosnow.setting.setValue(this.SPEED_UP_LEVEL, this.mSpeedLevel);
    moosnow.event.sendEventImmediately(EventType.SPEED_LEVEL_CHANGED, this.mSpeedLevel);
  }
  //每日签到
  getSignNum() {
    return moosnow.setting.getInt(this.SIGN_NUM, 0);
  }
  addSignNum() {
    moosnow.setting.appendInt(this.SIGN_NUM, 1);
    moosnow.setting.setValue(this.SIGN_DATE, moosnow.Common.formatTime(new Date()));
  }
  setVideoSign() {
    moosnow.setting.setValue(this.SIGN_VIDEO_DATE, moosnow.Common.formatTime(new Date()));
  }
  getIsSign() {
    let now = moosnow.Common.formatTime(new Date());
    return now == moosnow.setting.getString(this.SIGN_DATE, "");
  }
  getIsVideoSign() {
    let now = moosnow.Common.formatTime(new Date());
    return now == moosnow.setting.getString(this.SIGN_VIDEO_DATE, "");
  }

  // //皮肤管理
  getHasSkins(): number[] {
    return moosnow.setting.getObject(this.HAS_SKINS, null);
  }
  addHasSkins(v: number) {
    let arr = this.getHasSkins() || [];
    arr.push(v);
    this.setHasSkins(arr);
  }
  setHasSkins(v: number[]) {
    moosnow.setting.setObject(this.HAS_SKINS, v);
  }
  getIsHasSkin(v: number): boolean {
    let arr = this.getHasSkins();
    for (let i = 0, len = arr.length; i < len; i++) {
      if (arr[i] == v) return true;
    }
    return false;
  }

  getUnlockedSkins(): number[] {
    return moosnow.setting.getObject(this.UNLOCKED_SKINS, [0]);
  }
  addUnlockedSkins(v: number) {
    let arr = this.getUnlockedSkins() || [0];
    arr.push(v);
    this.setUnlockedSkins(arr);
  }

  subUnlockedSkins(v: number) {
    let arr = this.getUnlockedSkins();
    if (arr != null && arr.length > 0) {
      for (let i = 0; i < arr.length; i++) {
        if (arr[i] == v) {
          arr.splice(i, 1);
        }
      }
    }
    this.setUnlockedSkins(arr);
  }

  setUnlockedSkins(v: number[]) {
    moosnow.setting.setObject(this.UNLOCKED_SKINS, v);
  }
  getIsUnlockedSkin(v: number): boolean {
    let arr = this.getUnlockedSkins() || [];
    for (let i = 0, len = arr.length; i < len; i++) {
      if (arr[i] == v) return true;
    }
    return false;
  }

  //皮肤
  public getCurrentSkin(type: number) {
    if (type == 0) {
      return moosnow.setting.getInt(this.CURRENT_SKIN_0, 1);
    } else {
      return moosnow.setting.getInt(this.CURRENT_SKIN_1, 1);
    }
  }

  public setCurrentSkin(type: number, v: number) {
    if (type == 0) {
      moosnow.setting.setValue(this.CURRENT_SKIN_0, v);
    } else {
      moosnow.setting.setValue(this.CURRENT_SKIN_1, v);
    }
  }
}
