import BaseModule from "../BaseModule";

export default class Scene3DModule extends BaseModule {
  private scenePath: string = "subRes/LayaScene_";
  // private truePath: string = "";
  private loadingForm: string = "";
  private currentScene: Laya.Scene3D = null;
  private sceneLogic: any = null;
  constructor() {
    super();
  }
  public init(sceneLogic: any, loadingForm: string = "") {
    this.loadingForm = loadingForm;
    this.sceneLogic = sceneLogic;
    // this.truePath = Lite.platform.getPlatform() != 'pc' ? AppConfig.resUrl + this.scenePath : this.scenePath;
  }
  public preloadScene(scene: string) {
    return new Promise((resolve) => {
      if (!scene) {
        resolve();
      } else {
        let path = this._getPath(scene);
        Laya.Scene3D.load(
          `${path}${scene}/Conventional/${scene}.ls`,
          Laya.Handler.create(
            this,
            () => {
              resolve();
            },
            null,
            false
          )
        );
      }
    });
  }

  public loadScene(scene: string, isDestroy: boolean = true, isLoadingForm: boolean = true, callback: any = null) {
    let path = this._getPath(scene);
    // let level = Lite.data.getCurrentLevel();
    // Lite.http.point('加载关卡', { level: level });
    this._clearScene(isDestroy);
    let res = Laya.loader.getRes(`${path}${scene}/Conventional/${scene}.ls`);
    isLoadingForm && !res && this.loadingForm && Lite.ui.pushUIForm(this.loadingForm);
    this._loadScene(scene).then((retScene: Laya.Scene3D) => {
      retScene.addComponent(this.sceneLogic); //.init(retScene);
      let addScene = Laya.stage.addChild(retScene) as Laya.Scene3D;
     // addScene.zOrder = -1;
      this.currentScene = retScene;
      isLoadingForm && !res && this.loadingForm && Lite.ui.hideUIForm(this.loadingForm);
      // Lite.http.point('加载关卡完成', { level: level });
      if (callback) callback(retScene);
    });
  }
  private _loadScene(scene) {
    return new Promise((resolve) => {
      if (!scene) {
        resolve();
      } else {
        let path = this._getPath(scene);
        // Laya.loader.create(`${path}${scene}/Conventional/${scene}.ls`, Laya.Handler.create(this, (retScene: Laya.Scene3D) => {
        //     resolve(retScene);
        // }, null, false), Laya.Handler.create(this, (e) => {
        //     Lite.event.sendEventImmediately(EventType.LOADING_PROGRESS, e);
        // }, null, false), Laya.Sprite3D.HIERARCHY);

        Laya.Scene3D.load(
          `${path}${scene}/Conventional/${scene}.ls`,
          Laya.Handler.create(
            this,
            (retScene) => {
              resolve(retScene);
            },
            null,
            false
          )
        );
      }
    });
  }
  public _getPath(scene) {
    // let path = this.truePath;
    // if (scene == '1-1' || scene == '1-2' || scene == '1-3') {
    //     path = this.scenePath;
    // }
    // return path;
    return this.scenePath;
  }
  public _clearScene(isDestroy: boolean) {
    if (!this.currentScene) return;
    this.currentScene.removeSelf();
    // this.currentScene.destroy();
    this._destroy(this.currentScene);
    this.currentScene = null;
    if (isDestroy) {
      Laya.Resource.destroyUnusedResources();
    }
  }

  public _destroy(node: any) {
    if (node.numChildren > 0) {
      for (let i = 0; i < node.numChildren; i++) {
        let item = node.getChildAt(i) as any;
        this._destroy(item);
      }
      this._destroy(node);
    } else {
      node.destroy();
    }
  }
}
