import adItem from "../form/adItem";

export default class adItem3 extends adItem {
  private Sprite: Laya.Sprite;
  public scaleShake(caller: any, func: () => void) {
    if (null == this.Sprite) {
      this.Sprite = this.owner as Laya.Sprite;
    }
    Laya.Tween.to(
      this.Sprite,
      { scaleX: 1.2, scaleY: 1.2 },
      100,
      null,
      Laya.Handler.create(this, () => {
        var shaketimes: number = 100;
        var boo: boolean = false;
        func.apply(caller);
        Laya.timer.loop(
          10,
          this,
          () => {
            if (shaketimes > 0) {
              shaketimes--;
              if (false == boo) {
                this.Sprite.rotation--;
                if (this.Sprite.rotation < -15) {
                  boo = true;
                }
              } else {
                this.Sprite.rotation++;
                if (this.Sprite.rotation > 15) {
                  boo = false;
                }
              }
            } else {
              Laya.timer.clearAll(this);
              Laya.Tween.to(this.Sprite, { rotation: 0, scaleX: 1, scaleY: 1 }, 100, null);
            }
          },
          null,
          true,
          true
        );
      })
    );
  }
}
