import adItem_TopHot from "./adItem_ExportTopHot";
import { Ad_Base } from "./Ad_Base";
import ExportTool_Scroller from "./ExportTool_Scroller";
export default class Export_TopHot extends Ad_Base {
  private adList: ExportTool_TopHot;
  private continueBtn: Laya.Button;
  public static adStr: string[] = ["经典玩法,爽快战斗！", "与兄弟一起,共同征战洪荒世界！", "以爱之名,还原国民梦想", "酷炫技能,感受酣畅战斗", "广积粮,筑高墙,好友齐称王！", "我这一刀下去,BOSS都要飞！", "全民王者,震颤世界", "王总喊你一起打妖怪啦", "你的老板叫你上线打怪", "你的同学正在偷你家菜", "隔壁老王玩了都说好！", "过五关斩六将,你的好友要你一起来战", "燃炸！根本停不下来", "激爽战斗！一刀一个小朋友"];
  public static combineStrF: string[] = ["最火爆的", "本年度最佳", "本月人气最高", "人气飙升的", "朋友正在热玩的", "火爆朋友圈的", "超爽快的", "风靡全球的", "全新玩法的", "官方正版", "轻松简单的"];
  public static combineStrL: string[] = ["小游戏", "动作游戏", "射击游戏", "卡牌游戏", "模拟经营游戏", "剧情游戏", "策略战斗游戏", "真人对战游戏", "益智游戏", "多人游戏", "角色扮演游戏"];
  public onAwake() {
    this.adList = this.owner.getChildByName("AdList").addComponent(ExportTool_TopHot);
    this.adList.scrollX = false;
    this.continueBtn = this.owner.getChildByName("Top").getChildByName("Gray").getChildByName("ContinueBtn") as Laya.Button;
    this.continueBtn.on(Laya.Event.CLICK, this, () => {
      this.goNext.method.apply(this.goNext.caller, this.goNext.args);
    });
  }
  public onShow(inner: Laya.Handler) {
    super.onShow(inner);
    this.adList.reInitAd();
    Export_TopHot.adStr.sort(() => (Math.random() > 0.5 ? 1 : -1));
    Export_TopHot.combineStrF.sort(() => (Math.random() > 0.5 ? 1 : -1));
    Export_TopHot.combineStrL.sort(() => (Math.random() > 0.5 ? 1 : -1));
  }
}
export class ExportTool_TopHot extends ExportTool_Scroller {
  public couldChange: boolean = false;
  public onAwake() {
    this.adList = this.owner as Laya.List;
    this.adList.renderHandler = new Laya.Handler(this, this.updateItem);
  }
  public reInitAd() {
    this.couldChange = true;
    let tmp = Lite.config.getAd();
    this.adList.array = tmp;
    Laya.timer.once(1000, this, () => {
      this.couldChange = false;
    });
  }
  protected updateItem(cell: Laya.Box, index: number) {
    if (true == this.couldChange) {
      (cell.getComponent(adItem_TopHot) as adItem_TopHot).init(cell.dataSource, index + 1);
    } else {
      (cell.getComponent(adItem_TopHot) as adItem_TopHot).init2(cell.dataSource, index + 1);
    }
  }
}
