import EventType from "../../../Tool/EventType";
import adItem from "../form/adItem";
import UIForm from "../form/UIForm";
import MistouchBtn_Offset from "./MistouchBtn_Offset";
/**结算界面
 * pushform时传入是否胜利和获得的钱
 */
export abstract class GameOverFormFrame extends UIForm {
  /**胜利Sprite */
  protected winSprite: Laya.Sprite;
  /**失败Sprite */
  protected failSprite: Laya.Sprite;
  /**中间的四个导出广告 */
  private mainExportAd: Laya.List;
  /**增加的金钱 */
  private addMoneyFont: Laya.FontClip;
  /**视频按钮 */
  private adBtn: Laya.Sprite;
  /**视频按钮加的钱 */
  private adAddMoneyFont: Laya.FontClip;
  /**继续游戏按钮 */
  private continueBtn: Laya.Button;
  private continueBtnMisstouch: MistouchBtn_Offset;
  protected continueBtnRightbottom: number = 300;
  /**剔除顶部刘海部分和底部导出广告部分剩下的中间区域 */
  protected centerZone: Laya.Sprite;
  /** 本次获得的钱 */
  private curGetMoney: number = 0;
  public onAwake() {
    super.onAwake();
    this.centerZone = this.owner.getChildByName("CenterZone") as Laya.Sprite;
    this.winSprite = this.centerZone.getChildByName("WinSprite") as Laya.Sprite;
    this.failSprite = this.centerZone.getChildByName("FailSprite") as Laya.Sprite;
    this.mainExportAd = this.centerZone.getChildByName("MainExportAd") as Laya.List;
    this.addMoneyFont = this.centerZone.getChildByName("AddMoneyFont") as Laya.FontClip;
    this.adBtn = this.centerZone.getChildByName("AdBtn") as Laya.Sprite;
    this.adAddMoneyFont = this.adBtn.getChildByName("AdAddMoneyFont") as Laya.FontClip;
    this.continueBtn = this.centerZone.getChildByName("ContinueBtn") as Laya.Button;
    this.continueBtnMisstouch = this.continueBtn.addComponent(MistouchBtn_Offset);
    this.continueBtnMisstouch.Init(
      Laya.Handler.create(
        this,
        () => {
          Lite.ui.showToast("获得" + this.curGetMoney + "个金币");
          Lite.data.addCoin(this.curGetMoney);
          this.onClickContinueBtn();
        },
        null,
        false
      ),

      this.continueBtnRightbottom,
      Lite.config.RewardOffsetBanner
    );
    this.adBtn.on(Laya.Event.CLICK, this, () => {
      moosnow.platform.showVideo((res) => {
        switch (res) {
          case moosnow.VIDEO_STATUS.NOTEND:
            Lite.ui.showToast(moosnow.VIDEO_MSG.NOTEND);
            break;
          case moosnow.VIDEO_STATUS.ERR:
            Lite.ui.showToast(moosnow.VIDEO_MSG.ERR);
            break;
          case moosnow.VIDEO_STATUS.END:
            Lite.ui.showToast("获得" + this.curGetMoney * 3 + "个金币");
            Lite.data.addCoin(this.curGetMoney * 3);
            moosnow.event.sendEventImmediately(EventType.GAME_STATE_OVER);
            this.onClickContinueBtn();
            break;
          default:
            Lite.ui.showToast(moosnow.VIDEO_MSG.ERR);
            break;
        }
      });
    });
  }
  /**
   * @param isWin 是否胜利
   * @param getMoney 获得的金钱
   */
  public onShow(arg: { isWin: boolean; getMoney: number }) {
    this.winfailShowFunc(arg.isWin);
    moosnow.event.sendEventImmediately(EventType.GAME_STATE_END);
    this.curGetMoney = arg.getMoney;
    this.addMoneyFont.value = arg.getMoney.toString();
    this.adAddMoneyFont.value = (arg.getMoney * 3).toString();
    let adarr = Lite.config.getAd();
    for (let q = 0; q < this.mainExportAd.cells.length; q++) {
      (this.mainExportAd.cells[q].getComponent(adItem) as adItem).init(adarr[q]);
    }
    this.continueBtnMisstouch.Act = true;
    if (true == Lite.config.aldMonitorOn) {
      moosnow.http.endGame(Lite.data.getVirtualLevel().toString(), arg.isWin);
    }
    if (true == arg.isWin) {
      Lite.data.addVirtualLevel(1);
    }
  }
  public onHide() {
    this.continueBtnMisstouch.Act = false;
  }
  /**胜利/失败的标签显示的方法(有些项目可能会有一定的动画)
   * @param isWin 是否胜利
   */
  protected winfailShowFunc(isWin: boolean) {
    if (true == isWin) {
      this.failSprite.visible = false;
      this.winSprite.visible = true;
    } else {
      this.failSprite.visible = true;
      this.winSprite.visible = false;
    }
  }
  /**当按下继续游戏或者视频播完之后 */
  protected abstract onClickContinueBtn();
}
