import UIForm from "./UIForm";
var timeLine = new Laya.TimeLine();
export default class ToastForm extends UIForm {
  /** @prop {name:msgText, tips:"文本提示", type:Node}*/
  public msgText: Laya.Text = null;

  constructor() {
    super();
  }
  onAwake() {
    super.onAwake();
    // Common.setFullView(this.msgText);
  }
  show(msg: string) {
    let owner = this.owner as Laya.Sprite;
    Laya.timer.clear(this, this.hide);
    timeLine.reset();
    this.msgText.text = msg;
    owner.scale(1, 1);
    owner.visible = true;
    timeLine.to(owner, { scaleX: 1.2, scaleY: 1.2 }, 100).to(owner, { scaleX: 1, scaleY: 1 }, 100);
    timeLine.play();
    Laya.timer.once(1000, this, this.hide);
  }
  hide() {
    let owner = this.owner as Laya.Sprite;
    owner.visible = false;
  }
}
