import MistouchBtn_Flash from "../MistouchBtn_Flash";
import ExportTool_Scroller from "./ExportTool_Scroller";
import { Ad_Base } from "./Ad_Base";
import EventType from "../../../../Tool/EventType";
import { LinkedNode } from "../../../../Tool/DataStruct/index";
export default class Export_FriendHot extends Ad_Base {
  private adList: ExportTool_Scroller;
  private continueBtn: Laya.Button;
  private continueBtnFlashBanner: MistouchBtn_Flash;
  private fingerAnim: Laya.Animation = new Laya.Animation();
  public onAwake() {
    this.adList = (this.owner.getChildByName("AdList") as Laya.List).addComponent(ExportTool_Scroller);
    this.adList.scrollX = false;
    this.continueBtn = this.owner.getChildByName("ContinueBtn") as Laya.Button;
    this.continueBtnFlashBanner = this.continueBtn.addComponent(MistouchBtn_Flash);
    this.continueBtnFlashBanner.Init(
      Laya.Handler.create(
        this,
        () => {
          this.goNext.method.apply(this.goNext.caller, this.goNext.args);
        },
        null,
        false
      ),
      Lite.config.FlashBanner01
    );
    this.fingerAnim.loadAtlas(
      "subRes/atlas/comp/ShinningFinger.atlas",
      Laya.Handler.create(this, () => {
        this.owner.getChildByName("Anim").addChild(this.fingerAnim);
      })
    );
    (this.adList.owner as Laya.Clip).top;
  }
  public onShow(inner: Laya.Handler) {
    super.onShow(inner);
    this.fingerAnim.play(0, true);
    this.adList.Act = true;
    moosnow.event.sendEventImmediately(EventType.FriendHotShow);
    if (true == Lite.config.ForceSkip01) {
      Laya.timer.once(200, this, () => {
        let tmp = () => {
          moosnow.event.addListener(
            EventType.GameCenterHide,
            this,
            () => {
              moosnow.event.removeListener(EventType.ExportAchive, this);
              this.continueBtnFlashBanner.Act = true;
            },
            true
          );
        };
        moosnow.event.addListener(EventType.TurnToGameCenter, this, tmp, true);
        moosnow.event.addListener(
          EventType.ExportAchive,
          this,
          () => {
            moosnow.event.removeListener(EventType.GameCenterHide, this);
            this.continueBtnFlashBanner.Act = true;
          },
          true
        );
        this.adList.randomOnClick();
      });
    } else {
      this.continueBtnFlashBanner.Act = true;
    }
  }
  public onHide() {
    this.fingerAnim.stop();
    this.continueBtnFlashBanner.Act = false;
    this.adList.Act = false;
    moosnow.event.sendEventImmediately(EventType.FriendHotHide);
  }
}
