import EventType from "../../../Tool/EventType";

/**误点基类*/
export abstract class MistouchBtn_Base extends Laya.Script {
  protected innerFunc: Laya.Handler;
  protected buttonSelf: Laya.Button;
  protected preloadedBannerIdIndex;
  /**误点显示状态 */
  protected bannerMode: BannerMode = BannerMode.Close;
  /**误点默认状态 */
  protected bannerBaseMode: BannerMode = BannerMode.Close;
  /**已经初始化过了 */
  protected beenInit: boolean = false;
  /**显示banner */
  protected bannerShow() {
    moosnow.platform.showBanner(true, null, null, null, this.preloadedBannerIdIndex);
  }
  /**隐藏banner */
  protected bannerHide() {
    moosnow.event.sendEventImmediately(moosnow.PLATFORM_EVENT.ON_BANNER_HIDE);
    moosnow.platform.hideBanner();
  }
  /**运行调用
   * @param isAct 是否运行
   */
  public set Act(isAct: boolean) {
    if (true == isAct) {
      this.buttonSelf.on(Laya.Event.CLICK, this, this.btnEvent);
      if ("1" == Lite.config.allConfig.mistouchOn) {
        this.preloadedBannerIdIndex = moosnow.platform.preloadBanner();
        this.selectBannerShowType(this.bannerBaseMode);
      } else {
        this.selectBannerShowType(BannerMode.Close);
      }
    } else {
      this.buttonSelf.off(Laya.Event.CLICK, this, this.btnEvent);
      Laya.timer.clearAll(this);
      this.bannerHide();
    }
  }
  protected abstract selectBannerShowType(inner: BannerMode);
  protected abstract btnEvent();
}
/**误点模式 */
export enum BannerMode {
  /**关闭状态,开始就正常显示,能够正常调用传入的点击事件 */
  Close = 0,
  /** * 当界面打开时开始计时 #OffsetBannerDelayTime# 后出现
   * * 出现 #OffsetBannerContinueTime# 后才能正常调用传入的点击事件
   * * 单位:ms*/
  OnShow = 1,
  /** * 当第一次按下按钮开始计时 #OffsetBannerDelayTime# 后出现
   * * 出现 #OffsetBannerContinueTime# 后才能正常调用传入的点击事件
   * * 单位:ms*/
  OnFirstClickContinue = 2,
  /**在 #OnShow# 和 #OnFirstClickContinue# 中随机选择一个*/
  Random = 3,
  /**反正就是不准按 */
  NaN = 4,
}
