import EventType from "../../../../Tool/EventType";
import ProgressBar from "../../../../Tool/ProgressBar";
import { Random } from "../../../../Tool/Random";
import { Ad_Base } from "./Ad_Base";

//TODO:写狂点弹窗
export default class Export_CrazyClick extends Ad_Base {
  private ccSelf: Laya.Sprite;
  private smashAnim: Laya.Animation;
  private openAnim: Laya.Animation;
  private standByAnim: Laya.Animation;
  private crazyClickBtn: Laya.Button;
  private crazyClickArrow: Laya.Image;
  private crazyClickProgress: ProgressBar;
  private tweenProgress: number = 0;
  private isSmashing: boolean = false;
  private counter: number = 0;
  private smashCount: number = 0;
  private couldSmash: boolean = true;
  private ccTips: Laya.Image;
  private ccDarkBase: Laya.Image;
  private ccContinueBtn: Laya.Button;
  private ccText: Laya.Text;
  private ccTimes: number = 0;
  private ccTimesLimit: number = 0;
  private preloadBannerIndex;
  public onAwake() {
    super.onAwake();
    this.ccTips = this.owner.getChildByName("CCTips") as Laya.Image;
    this.ccDarkBase = this.owner.getChildByName("DarkBase") as Laya.Image;
    this.ccContinueBtn = this.ccTips.getChildByName("ContinueBtn") as Laya.Button;
    this.ccText = this.owner.getChildByName("CCText") as Laya.Text;
    this.ccSelf = this.owner.getChildByName("CCSelf") as Laya.Sprite;
    this.crazyClickBtn = this.ccSelf.getChildByName("CCBtn") as Laya.Button;
    this.crazyClickBtn.on(Laya.Event.CLICK, this, () => {
      if (true == this.couldSmash) {
        this.standByAnim.stop();
        // if (Laya.timer.currFrame - this.smashCount > 15) {
        //     this.smashCount = Laya.timer.currFrame;
        this.smashAnim.play(0, false);
        Laya.SoundManager.playSound("subRes/audio/smash.mp3");
        //  }
        this.tweenProgress += 0.15;
        this.ccTimes++;
        if (this.ccTimes == this.ccTimesLimit) {
          switch (Lite.config.mistouchNum) {
            case 1:
              console.log("Export_CrazyClick:弱狂点");
              moosnow.platform.showBanner(
                true,
                (inner: boolean) => {
                  if (true == inner) {
                    moosnow.platform.hideBanner();
                    moosnow.event.sendEventImmediately(moosnow.PLATFORM_EVENT.ON_BANNER_HIDE);
                    this.ccClear();
                  }
                },
                moosnow.BANNER_HORIZONTAL.CENTER,
                moosnow.BANNER_VERTICAL.BOTTOM,
                this.preloadBannerIndex
              );
              Laya.timer.once(1500, this, () => {
                moosnow.platform.hideBanner();
                moosnow.event.sendEventImmediately(moosnow.PLATFORM_EVENT.ON_BANNER_HIDE);
              });
              break;
            case 2:
              console.log("Export_CrazyClick:强狂点");
              this.ccTimes = 0;
              moosnow.platform.showBanner(
                true,
                (inner: boolean) => {
                  if (true == inner) {
                    moosnow.platform.hideBanner();
                    moosnow.event.sendEventImmediately(moosnow.PLATFORM_EVENT.ON_BANNER_HIDE);
                    this.ccClear();
                  }
                },
                moosnow.BANNER_HORIZONTAL.CENTER,
                moosnow.BANNER_VERTICAL.BOTTOM,
                this.preloadBannerIndex
              );
              Laya.timer.once(1500, this, () => {
                moosnow.platform.hideBanner();
                moosnow.event.sendEventImmediately(moosnow.PLATFORM_EVENT.ON_BANNER_HIDE);
              });
              break;
            default:
              console.log("Export_CrazyClick:狂点关闭");
              break;
          }
        }
        if (this.tweenProgress >= 1) {
          this.ccClear();
        }
        this.isSmashing = true;
        this.counter = 0;
      }
    });
    this.crazyClickArrow = this.ccSelf.getChildByName("CCArrow") as Laya.Image;
    this.crazyClickProgress = this.ccSelf.getChildByName("Bar").getComponent(ProgressBar);
    this.smashAnim = this.owner["Smash"] as Laya.Animation;
    this.openAnim = this.owner["Open"] as Laya.Animation;
    this.standByAnim = this.owner["StandBy"] as Laya.Animation;
    this.ccContinueBtn.on(Laya.Event.CLICK, this, () => {
      this.getGift();
    });
  }
  public onShow(inner: Laya.Handler) {
    super.onShow(inner);
    if (0 == Lite.config.mistouchNum || Lite.data.getVirtualLevel() < Lite.config.CrazyStartLevel || Lite.data.getVirtualLevel() % Lite.config.mistouchInterval != 0) {
      console.log("狂点关闭:mistouchNum", Lite.config.mistouchNum, "CrazyStartLevel:", Lite.config.CrazyStartLevel, "mistouchInterval:", Lite.config.mistouchInterval, "Level:", Lite.data.getVirtualLevel());
      (this.owner as Laya.Sprite).visible = false;
      //pushuiform的回调会在onshow之后执行,所以当狂点功能关闭时瞬间关闭会造成一些次序问题,这里延后两帧执行
      Laya.timer.frameOnce(2, this, () => {
        this.goNext.method.apply(this.goNext.caller, this.goNext.args);
      });
    } else {
      console.log("狂点打开");
      //   this.ccSelf.scale(0, 0);
      //   Laya.Tween.to(this.ccSelf, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.elasticOut);
      this.ccTips.visible = false;
      this.ccDarkBase.visible = false;
      this.couldSmash = true;
      this.standByAnim.play(0, true);
      Laya.timer.loop(20, this, this.update);
      this.arrowTween();
      this.ccTimesLimit = Random.rangeFloor(2, 9);
      this.preloadBannerIndex = moosnow.platform.preloadBanner();
    }
  }
  private update() {
    if (true == this.isSmashing) {
      this.counter++;
      if (this.counter > 20) {
        this.standByAnim.play(0, true);
        this.smashAnim.stop();
        this.counter = 0;
        this.isSmashing = false;
      }
    }
    if (this.tweenProgress > 0) {
      this.tweenProgress -= 0.005;
    }
    this.crazyClickProgress.Value = this.crazyClickProgress.Value + 0.5 * (this.tweenProgress - this.crazyClickProgress.Value);
  }
  public onHide() {
    this.ccTimes = 0;
    this.tweenProgress = 0;
    this.crazyClickProgress.Value = 0;
    this.counter = 0;
    this.standByAnim.stop();
    this.openAnim.stop();
    this.smashAnim.stop();
    this.couldSmash = false;
    Laya.timer.clearAll(this);
    Laya.Tween.clearAll(this.crazyClickArrow);
  }
  private ccClear() {
    this.onHide();
    this.openAnim.play(0, false);
    Laya.SoundManager.playSound("subRes/audio/getTreasure.mp3");
    Laya.timer.once(1500, this, () => {
      this.ccTips.visible = true;
      this.ccDarkBase.visible = true;
      this.ccTips.scale(0, 0);
      Laya.Tween.to(this.ccTips, { scaleX: 1, scaleY: 1 }, 1000, Laya.Ease.elasticOut);
    });
  }
  private arrowTween() {
    Laya.Tween.to(
      this.crazyClickArrow,
      { bottom: 310 },
      200,
      null,
      Laya.Handler.create(this, () => {
        Laya.Tween.to(
          this.crazyClickArrow,
          { bottom: 360 },
          200,
          null,
          Laya.Handler.create(this, () => {
            this.arrowTween();
          })
        );
      })
    );
  }
  private getGift() {
    moosnow.event.sendEventImmediately(EventType.CrazyClickGift);
    this.goNext.method.apply(this.goNext.caller, this.goNext.args);
  }
}
