import GameDataCenter from "./GameDataCenter";
import AppConfig from "./Module/AppConfig";
import AudioModule from "./Module/AudioModule";
import Scene3DModule from "./Module/Scene3DModule";
import { UIModule } from "./Module/UIModule";

export class InitLite {
  constructor() {
    window["Lite"] = this;
    this.mData = new GameDataCenter();
  }
  private mData: GameDataCenter;
  public get data() {
    return this.mData;
  }
  private mUi: UIModule = new UIModule();
  public get ui() {
    return this.mUi;
  }
  private mScene3D: Scene3DModule = new Scene3DModule();
  public get scene3D() {
    return this.mScene3D;
  }
  private mConfig: AppConfig = new AppConfig();
  public get config() {
    return this.mConfig;
  }
  private mAudio: AudioModule = new AudioModule();
  public get audio() {
    return this.mAudio;
  }
}
