import EventType from "../../../../Tool/EventType";
import MistouchBtn_Offset from "../MistouchBtn_Offset";
import { Ad_Base } from "./Ad_Base";

/**皮肤试用界面
 TODO:修改试用方法调用形式
 */
export default class Ad_TrySkin extends Ad_Base {
  public skinShower: Laya.Sprite;
  private bg: Laya.Sprite;
  private trySkinBtn: Laya.Button;
  private continueBtn: MistouchBtn_Offset;
  private rollerSprite: Laya.Sprite;
  public onAwake() {
    this.bg = this.owner.getChildByName("Bg") as Laya.Sprite;
    this.rollerSprite = this.bg.getChildByName("roller") as Laya.Sprite;
    this.skinShower = this.bg.getChildByName("SkinShow") as Laya.Sprite;
    this.trySkinBtn = this.bg.getChildByName("AdBtn") as Laya.Button;
    this.trySkinBtn.on(Laya.Event.CLICK, this, () => {
      moosnow.platform.showVideo((res) => {
        switch (res) {
          case moosnow.VIDEO_STATUS.NOTEND:
            Lite.ui.showToast(moosnow.VIDEO_MSG.NOTEND);
            break;
          case moosnow.VIDEO_STATUS.ERR:
            Lite.ui.showToast(moosnow.VIDEO_MSG.ERR);
            break;
          case moosnow.VIDEO_STATUS.END:
            moosnow.event.sendEventImmediately(EventType.TrySkinGift);
            this.goNext.method.apply(this.goNext.caller, this.goNext.args);
            break;
          default:
            Lite.ui.showToast(moosnow.VIDEO_MSG.ERR);
            break;
        }
      });
    });
    this.continueBtn = this.owner.getChildByName("ContinueBtn").addComponent(MistouchBtn_Offset);
    this.continueBtn.Init(
      Laya.Handler.create(
        this,
        () => {
          if (true == Lite.config.SkinForceAd) {
            moosnow.platform.showVideo((res) => {
              this.goNext.method.apply(this.goNext.caller, this.goNext.args);
            });
          } else {
            this.goNext.method.apply(this.goNext.caller, this.goNext.args);
          }
        },
        null,
        false
      ),
      300,
      Lite.config.SkinWudian
    );
  }
  public onShow(inner: Laya.Handler) {
    super.onShow(inner);
    this.continueBtn.Act = true;
    Laya.timer.loop(20, this, this.roller);
    this.bg.scale(0, 0);
    Laya.Tween.to(this.bg, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.elasticOut);
  }
  public onHide() {
    this.continueBtn.Act = false;
    Laya.timer.clearAll(this);
  }
  private roller() {
    this.rollerSprite.rotation += this.rollerSprite.rotation == 360 ? -360 : 1;
  }
}
