import moosnowAdRow from "../../../../libs/model/moosnowAdRow";
import EventType from "../../../Tool/EventType";
import { Random } from "../../../Tool/Random";

export default class adItem extends Laya.Script {
  public appItem: moosnowAdRow = null;
  private redDot: Laya.Sprite;
  public set redDotShow(inner: boolean) {
    if (null == this.redDot) {
      this.redDot = this.owner.getChildByName(Random.Boolean?"RedDot":"RedDot1") as Laya.Sprite;
    } else {
      this.redDot.visible = inner;
    }
  }
  constructor() {
    super();
  }
  onEnable(): void {
    this.owner.on(Laya.Event.CLICK, this, this.onAdClick);
    this.getBgColor();
  }

  public currentAdData: moosnowAdRow = null;
  init(appItem: moosnowAdRow) {
    this.currentAdData = appItem;
    let img: Laya.Image = this.owner.getChildByName("img") as Laya.Image;
    let title: Laya.Text = this.owner.getChildByName("title") as Laya.Text;
    this.appItem = appItem;
    title && appItem && (title.text = appItem.title);
    img && appItem && (img.skin = appItem.img);
  }
  getBgColor() {
    let bgImg: Laya.Image = this.owner.getChildByName("img_bg") as Laya.Image;
    if (bgImg) {
      let i = Random.rangeFloor(1, 4);
      bgImg.skin = "comp/base" + i + ".png";
    }
  }
  onDisable(): void {
    this.owner.off(Laya.Event.CLICK, this, this.onAdClick);
  }
  onAdClick() {
    if (null != this.appItem) {
      console.log(`navigatTomini ${this.appItem.title} ${this.appItem.appid}`);
      moosnow.event.sendEventImmediately(EventType.ExportStart);
      moosnow.platform.navigate2Mini(
        this.appItem,
        () => {
          console.log("小游戏跳转成功");
          moosnow.event.sendEventImmediately(EventType.ExportAchive);
        },
        () => {
          console.log("小游戏跳转失败");
          moosnow.event.sendEventImmediately(EventType.TurnToGameCenter);
        }
      );
    } else {
      console.error("没有跳转数据->appItem:", this.appItem);
    }
  }
  public setCross() {
    for (let q = 0; q < this.owner.numChildren; q++) {
      (this.owner.getChildAt(q) as Laya.Sprite).visible = false;
    }
    (this.owner.getChildAt(1) as Laya.Image).visible = true;
    (this.owner.getChildAt(1) as Laya.Image).skin = "comp/Boutique/x.png";
  }
}
