import EventType from "../../../Tool/EventType";
import { LoadingViewFrame } from "./LoadingViewFrame";

export default class LoadingView extends LoadingViewFrame {
  protected preLoadPacList: string[] = ["subRes"];
  protected preLoadResList: { url: string; type: string }[] = [
    // { url: "subRes/SceneCreate.json", type: Laya.Loader.JSON },
    //  { url: "subRes/Config.json", type: Laya.Loader.JSON },
  ];
  private ProgressNum: Laya.FontClip;
  protected onAwaken() {
    this.ProgressNum = this.owner.getChildByName("loading") as Laya.FontClip;
  }
  protected onEnabled() {}
  protected onProgressBarValueChange(innerValueChangeNum: number) {
    this.ProgressNum.value = Math.floor(innerValueChangeNum * 100).toString();
  }
  protected onProgressLoadComplete() {
    console.log("整体进度读取完毕:");
    moosnow.event.sendEventImmediately(EventType.CLOSE_LOADINGVIEW);
    // Lite.scene3D.init(SceneMgr);
    // Lite.scene3D.loadScene("Scene", false, false, () => {
    // });
  }
  protected onConfigLoaded(innerRes: any): void {
    console.log("配置读取完毕:", innerRes);
  }
}
