import moosnowResult from "../../../libs/model/moosnowResult";
import BaseModule from "../BaseModule";
import { Random } from "../Random";

export default class AppConfig extends BaseModule {
  constructor() {
    super();
  }

  public mistouchPosNum: number = 0;
  public bannerShowCountLimit: number = 10;
  public mAdData: moosnowResult = null;
  public gameNum: number = 0;
  public gameNumPos: number = 0;

  /////////////////////////////竖版新通用View使用/////////////////////////////////////
  private _allConfig: any;
  public set allConfig(inner) {
    this._allConfig = inner;
    this.Site01 = [6];
    //  this._allConfig.site01 && (this.Site01 = this._allConfig.site01);
    this._allConfig.site02 && (this.Site02 = this._allConfig.site02);
    this._allConfig.site03 && (this.Site03 = this._allConfig.site03);
    this._allConfig.site03 && (this.Site04 = this._allConfig.site04);
    this.mistouchNum = Number(this._allConfig.mistouchNum);
    this.CrazyStartLevel = Number(this._allConfig.CrazyStartLevel);
    this.mistouchInterval = Number(this._allConfig.mistouchInterval);
    this.FlashBanner01 = Number(this._allConfig.FlashBanner01);
    this.RewardOffsetBanner = Number(this._allConfig.RewardOffsetBanner);
    this.GGPopWudian = Number(this._allConfig.GGPopWudian);
    this.SkinWudian = Number(this._allConfig.SkinWudian);
    this.GameCenterWudian = Number(this._allConfig.GameCenterWudian);
    this.OffsetBannerDelayTime = Number(this._allConfig.OffsetBannerDelayTime) * 1000;
    this.OffsetBannerContinueTime = Number(this._allConfig.OffsetBannerContinueTime) * 1000;
    this.FlashBannerDelayTime = Number(this._allConfig.FlashBannerDelayTime);
    this.FlashBannerContinueTime = Number(this._allConfig.FlashBannerContinueTime);
    this.ForceSkip01 = this._allConfig.ForceSkip01 == "1" ? true : false;
    this.ForceSkip02 = this._allConfig.ForceSkip02 == "1" ? true : false;
    this.SliceSkip = this._allConfig.SliceSkip == "1" ? true : false;
    this.CancelToSkip = this._allConfig.CancelToSkip == "1" ? true : false;
    this.SkinForceAd = this._allConfig.SkinForceAd == "1" ? true : false;
    this.aldMonitorOn = this._allConfig.aldMonitorOn == "1" ? true : false;
  }
  public get allConfig() {
    return this._allConfig;
  }
  public aldMonitorOn: boolean = false;
  public Site01: number[] = [1];
  public Site02: number[] = [2];
  public Site03: number[] = [3];
  public Site04: number[] = [4];
  public mistouchNum: number = 1;
  public CrazyStartLevel: number = 1;
  public mistouchInterval: number = 1;
  public FlashBanner01: number = 1;
  public RewardOffsetBanner: number = 1;
  public GGPopWudian: number = 1;
  public SkinWudian: number = 1;
  public GameCenterWudian: number = 1;
  public OffsetBannerDelayTime: number = 1000;
  public OffsetBannerContinueTime: number = 1000;
  public FlashBannerDelayTime: number = 1000;
  public FlashBannerContinueTime: number = 1000;
  public ForceSkip01: boolean = false;
  public ForceSkip02: boolean = false;
  public SliceSkip: boolean = false;
  public CancelToSkip: boolean = false;
  public SkinForceAd: boolean = false;
  ////////////////////////////竖版新通用View使用///////////////////////////////////////
  onEnable() {}
  public getAd() {
    let arr = [...this.adData.indexLeft];
    return arr.sort(() => (Math.random() > 0.5 ? 1 : -1));
  }
  public getRandomAd(source?) {
    let arr = [];
    if (source) arr = source;
    else arr = [...this.mAdData.indexLeft];
    let i = Random.range(0, arr.length - 1);
    return arr[i];
  }
  public set adData(v) {
    this.mAdData = v;
  }
  public get adData() {
    this.mAdData.indexLeft.sort(() => (Math.random() > 0.5 ? 1 : -1));
    return this.mAdData;
  }
  public addGameNum() {
    this.gameNum++;
    this.gameNumPos++;
  }
  public isMistouch() {
    // return false;
    return this.mistouchNum;
  }
  public isMistouchPos() {
    // return false;
    if (this.mistouchPosNum != 0) {
      return true;
    }
    return false;
  }
  public resetGameNum() {
    if (this.isMistouch()) this.gameNum = 0;
    if (this.isMistouchPos()) this.gameNumPos = 0;
  }
}
