  //                     ownSprite.loadImage(imgUrl);
  //             }

  //         if (btnConfig.pos) {

  //             if (!isNaN(btnConfig.pos.x) && !isNaN(btnConfig.pos.y))
  //                 ownSprite.pos(btnConfig.pos.x, btnConfig.pos.y)
  //             if ("center" == btnConfig.pos.x)
  //                 ownSprite.x = (ownSprite.parent as any).width / 2;
  //             if ("center" == btnConfig.pos.y)
  //                 ownSprite.y = (ownSprite.parent as any).height / 2;
  //         }
  //         if (btnConfig.point) {
  //             this.enableLogPoint = btnConfig.point.enable;
