import { SiteMgr } from "./ExportVIews/SiteMgr";
import ProgressBar from "../../../Tool/ProgressBar";
import EventType from "../../../Tool/EventType";
import { InitLite } from "../../../Tool/InitLite";

/**
 * * 读取界面 
 * * 该界面加载时会加载定义到preLoadPacList的分包名
 * * 界面会在加载完分包后读取preLoadResList的文件到loader里
 * * 该界面不会自动关闭,需要手动发送事件来关闭
 @example
 Lite.event.sendEventImmediately(EventType.CLOSE_LOADINGVIEW);
 */
export abstract class LoadingViewFrame extends Laya.Script {
  /**进度条 */
  protected progressBar: ProgressBar;
  /**游戏运行必要分包统一加载包名 */
  protected abstract preLoadPacList: string[] = [
    // "subRes1","subRes2"
  ];
  /** Json,图片等必要文件统一加载信息
   * @param url bin目录下的对应文件路径,
   * @param type 文件类型(Laya.Loader.JSON等)
   * @example
   *  [{ url: "subRes/SceneCreate.json", type: Laya.Loader.JSON }]
   */
  protected abstract readonly preLoadResList: { url: string; type: string }[] = [];
  /**当前正在加载的分包 */
  private curLoadingPackage: number = 0;
  /**当前进度条显示的进度是真实的进度 */
  private isRealLoadingBar: boolean = false;
  /** 字符跳动*/
  private characterList: Laya.Sprite[] = [];
  /**进度条tween动画 */
  private loadTween: Laya.Tween;
  /**跳动字的浮动高度 */
  protected characterFloatHeight: number = 40;
  public onAwake() {
    new InitLite();
    LoadingViewFrame.setFullView(this.owner);
    this.progressBar = this.owner.getChildByName("progressBar").getComponent(ProgressBar);
    let self = this;
    this.progressBar.OnValueChanged.push((innerValueChangeNum) => {
      self.onProgressBarValueChange(innerValueChangeNum);
    });
    moosnow.event.addListener(EventType.CLOSE_LOADINGVIEW, this, () => {
      Laya.Tween.to(
        this.progressBar,
        { Value: 1 },
        1000,
        null,
        Laya.Handler.create(this, () => {
          moosnow.event.sendEventImmediately(EventType.GotoNextProgress);
          Laya.timer.once(200, this, () => {
            (this.owner as Laya.Scene).autoDestroyAtClosed = true;
            (this.owner as Laya.Scene).close();
            for (let q = 0; q < this.characterList.length; q++) {
              Laya.Tween.clearAll(this.characterList[q]);
            }
          });
        })
      );
    });
    var tmp = this.owner.getChildByName("characterList") as Laya.Node;
    if (tmp != null) {
      for (let q = 0; q < tmp.numChildren; q++) {
        this.characterList.push(tmp.getChildAt(q) as Laya.Sprite);
      }
      if (this.characterList.length > 0) {
        this.characterFloat(0);
      }
    }
    this.onAwaken();
  }
  public onEnable() {
    this.loadAll();
    var self = this;
    self.loadTween = Laya.Tween.to(
      self.progressBar,
      { Value: 0.6 },
      2000,
      null,
      Laya.Handler.create(this, () => {
        self.isRealLoadingBar = true;
        //self.onFakeLoadComplete.forEach((inner)=>{});
      })
    );
    this.onEnabled();
  }
  /**加载资源 */
  private loadData(self: LoadingViewFrame) {
    if (null != self.preLoadResList && self.preLoadResList.length > 0) {
      console.log("LoadingView:开始预加载文件:", self.preLoadResList);
      Laya.loader.load(
        self.preLoadResList,
        Laya.Handler.create(self, (ok) => {
          if (true == ok) {
            self.progressBar.Value = 0.95;
            Laya.timer.once(100, self, () => {
              LoadingViewFrame.setChildFullView(self.owner);
              console.log("LoadingView:文件预加载完成");
              self.onProgressLoadComplete();
            });
          } else {
            console.error("LoadingView:文件加载出错,检查填写路径");
          }
        }),
        Laya.Handler.create(self, (res) => {
          if (true == self.isRealLoadingBar) {
            self.progressBar.Value = 0.85 + 0.1 * res;
          }
        })
      );
    } else {
      self.progressBar.Value = 0.95;
      Laya.timer.once(100, self, () => {
        // LoadingViewFrame.setChildFullView(self.owner);
        console.log("LoadingView:没有文件需要预加载");
        self.onProgressLoadComplete();
      });
    }
  }
  /**加载分包 */
  private packageLoader(self: LoadingViewFrame) {
    if (window["wx"]) {
      if (self.preLoadPacList != null && self.curLoadingPackage < self.preLoadPacList.length) {
        self.loadPackage(
          self.preLoadPacList[self.curLoadingPackage],
          self,
          (res) => {
            self.curLoadingPackage++;
            self.packageLoader(self);
          },
          (res) => {
            self.packageLoader(self);
          }
        );
      } else {
        self.LoadPackageComplete(self);
      }
    } else {
      self.LoadPackageComplete(self);
    }
  }
  /**加载分包完毕时调用 */
  private LoadPackageComplete(self: LoadingViewFrame) {
    if (self.isRealLoadingBar != true) {
      self.loadTween.to(
        self.progressBar,
        { Value: 0.8 },
        300,
        null,
        Laya.Handler.create(self, () => {
          self.isRealLoadingBar = true;
          self.loadData(self);
        })
      );
    } else {
      self.loadData(self);
    }
  }
  /**加载单个分包
   * @param packageName 分包名
   * @param caller 调用者
   * @param onSuccess 当调用成功时
   * @param onFail 当调用失败时
   * @param res 读取任务对象
   */
  private loadPackage(packageName: string, caller: LoadingViewFrame, onSuccess: (res) => void, onFail: (res) => void) {
    var loadSubResTask: any = Laya.Browser.window["wx"].loadSubpackage({
      name: packageName,
      success: onSuccess,
      fail: onFail,
    });
    if (true == this.isRealLoadingBar) {
      loadSubResTask.onProgressUpdate((res) => {
        // 加载进度百分比
        var progress = res["progress"];
        caller.progressBar.Value = 0.6 + 0.2 * (progress / caller.preLoadPacList.length) + 0.2 * caller.curLoadingPackage;
      });
    }
  }
  /**加载开始入口 */
  private loadAll() {
    moosnow.platform.login(() => {
      console.log("LoadingView:登录成功");
      let a = new SiteMgr();
      moosnow.ad.getAd((res) => {
        console.log("LoadingView:广告数据 ", res);
        Lite.config.adData = res;
        moosnow.http.getAllConfig((res) => {
          console.log("LoadingView:游戏的所有配置数据 ", res);
          Lite.config.allConfig = res;
          //Lite.config.mistouchNum = res.mistouchNum;
          //Lite.config.mistouchPosNum = res.mistouchPosNum;
          this.onConfigLoaded(res);
          this.packageLoader(this);
        });
      });
    });
  }
  /**字符循环跳动 */
  private characterFloat(Counter: number) {
    let tmps = this.characterList[Counter];
    let tmpy = tmps.y;
    Laya.Tween.to(
      tmps,
      { y: tmpy - this.characterFloatHeight },
      80,
      null,
      Laya.Handler.create(this, () => {
        Laya.Tween.to(tmps, { y: tmpy }, 80, null);
        this.characterFloat(Counter + 1 == this.characterList.length ? 0 : Counter + 1);
      })
    );
  }
  /**加载完毕 */
  private loadComplete() {
    this.onProgressLoadComplete();
  }
  /**当进度条数值改变时调用的方法
   * @param innerValueChangeNum 进度条数值改变时传入的数值:0-1
   */
  protected abstract onProgressBarValueChange(innerValueChangeNum: number);
  /**当加载完毕时调用方法 */
  protected abstract onProgressLoadComplete();
  /**当加载完config调用
   * @param innerRes 从墨雪后台传入的表,具体对照参照后台配置
   */
  protected abstract onConfigLoaded(innerRes);
  /**防止不小心重写onAwake */
  protected abstract onAwaken();
  /**防止不小心重写onEnable */
  protected abstract onEnabled();
  static setCenter(obj: any) {
    let sp: Laya.Sprite = obj;
    sp.pos((Laya.stage.width - sp.width) / 2, (Laya.stage.height - sp.height) / 2);
  }

  static setFullView(obj: any) {
    let owner: Laya.Sprite = obj;
    owner.width = Laya.stage.width;
    owner.height = Laya.stage.height;
  }

  static setChildFullView(parent: Laya.Node) {
    this.setFullView(parent);
    for (let i = 0; i < parent.numChildren; i++) {
      let child = parent.getChildAt(i);
      // if(child typeof Laya.Image)
      if (child instanceof Laya.Image) continue;

      this.setFullView(child);
      if (child.numChildren > 0) this.setChildFullView(child);
    }
  }
}
