import adItem from "../../form/adItem";
import MistouchBtn_Flash from "../MistouchBtn_Flash";
import { Ad_Base } from "./Ad_Base";
export default class Export_Boutique extends Ad_Base {
  private continueBtn: MistouchBtn_Flash;
  private adList: Laya.List;
  private bg: Laya.Sprite;
  public onAwake() {
    super.onAwake();
    this.continueBtn = this.owner.getChildByName("ContinueBtn").addComponent(MistouchBtn_Flash);
    this.continueBtn.Init(
      Laya.Handler.create(
        this,
        () => {
          this.goNext.method.apply(this.goNext.caller, this.goNext.args);
        },
        null,
        false
      ),
      Lite.config.GGPopWudian
    );
    this.bg = this.owner.getChildByName("Bg") as Laya.Sprite;
    this.adList = this.bg.getChildByName("AdList") as Laya.List;
  }
  public onShow(inner) {
    super.onShow(inner);
    let arr = Lite.config.getAd();
    for (let q = 0; q < this.adList.cells.length; q++) {
      (this.adList.cells[q].getComponent(adItem) as adItem).init(arr[q]);
    }
    this.adList.cells[this.adList.cells.length - 1].getComponent(adItem).setCross();
    this.continueBtn.Act = true;
    this.bg.scale(0, 0);
    Laya.Tween.to(this.bg, { scaleX: 1, scaleY: 1 }, 500, Laya.Ease.elasticOut);
  }
  public onHide() {
    this.continueBtn.Act = false;
  }
}
