import moosnowAdRow from "../../../../../libs/model/moosnowAdRow";
import { Random } from "../../../../Tool/Random";
import adItem from "../../form/adItem";
import Export_TopHot from "./Export_TopHot";

export default class adItem_TopHot extends adItem {
  private img: Laya.Image;
  private title: Laya.Text;
  private star: Laya.Text;
  private popular: Laya.Text;
  private desc: Laya.Text;
  private playNow: Laya.Button;
  public counter: Laya.Text;
  onEnable() {}
  init(appItem: moosnowAdRow, innerNumber?: number) {
    this.img = this.owner.getChildByName("img") as Laya.Image;
    this.title = this.owner.getChildByName("title") as Laya.Text;
    this.star = this.owner.getChildByName("star") as Laya.Text;
    this.popular = this.owner.getChildByName("popular") as Laya.Text;
    this.desc = this.owner.getChildByName("desc") as Laya.Text;
    this.playNow = this.owner.getChildByName("playNow") as Laya.Button;
    this.playNow.on(Laya.Event.CLICK, this, this.onAdClick);
    this.counter = this.owner.getChildByName("counter") as Laya.Text;
    this.currentAdData = appItem;
    this.appItem = appItem;
    this.title && (this.title.text = this.appItem.title);
    this.img.skin = this.appItem.img;
    this.star.text = Random.range(8, 10 - innerNumber * 0.1).toFixed(1);
    this.popular.text = Random.rangeFloor(30, 200 - innerNumber).toString() + "w";
    this.counter.text = innerNumber.toString();
    if (innerNumber % 2 == 0) {
      let Count = innerNumber;
      while (Count > Export_TopHot.adStr.length) {
        Count -= Export_TopHot.adStr.length;
      }
      this.desc.text = Export_TopHot.adStr[Count - 1];
    } else {
      let Count = innerNumber;
      while (Count > Export_TopHot.combineStrF.length) {
        Count -= Export_TopHot.combineStrF.length;
      }
      this.desc.text = Export_TopHot.combineStrF[Count - 1] + Export_TopHot.combineStrL[Count - 1];
    }
  }
  init2(appItem: moosnowAdRow, innerNumber?: number) {
    this.img = this.owner.getChildByName("img") as Laya.Image;
    this.title = this.owner.getChildByName("title") as Laya.Text;
    this.star = this.owner.getChildByName("star") as Laya.Text;
    this.popular = this.owner.getChildByName("popular") as Laya.Text;
    this.desc = this.owner.getChildByName("desc") as Laya.Text;
    this.playNow = this.owner.getChildByName("playNow") as Laya.Button;
    this.playNow.on(Laya.Event.CLICK, this, this.onAdClick);
    this.counter = this.owner.getChildByName("counter") as Laya.Text;
    this.currentAdData = appItem;
    this.appItem = appItem;
    this.title && (this.title.text = this.appItem.title);
    this.img.skin = this.appItem.img;
    this.counter.text = innerNumber.toString();
  }
}
