export default class ProgressBar extends Laya.Script {
  private BackGround: Laya.Image;
  private Bar: Laya.Sprite;
  private BarSelf: Laya.Sprite;
  private Sprite: Laya.Sprite;
  public OnValueChanged: Array<(innerValue: number) => void> = [];
  /** @prop {name:_Value, tips:"默认值",type:sNumber,min:0,max:1,default:0}*/
  private _Value: number = 0;
  /** @prop {name:_Active, tips:"是否默认激活", type:Bool}*/
  private _Active: boolean;
  public onAwake() {
    this.Sprite = this.owner as Laya.Sprite;
    this.BackGround = this.owner as Laya.Image;
    this.BarSelf = this.BackGround.getChildAt(0) as Laya.Image;
    this.Bar = (this.BackGround.getChildAt(0) as Laya.Sprite).mask;
    //let a = this.Bar.mask;
    if (false == this._Active) this.Sprite.alpha = 0;
  }
  public set Value(inner: number) {
    this._Value = inner;
    this._Value = this._Value >= 1 ? 1 : this._Value;
    this._Value = this._Value <= 0 ? 0 : this._Value;
    //this.Bar.right = this.BackGround.width * (1 - this._Value);
    this.Bar.width = this.BarSelf.width * this.Value;
    //this.Bar.x = this.Bar.width / 2;
    this.OnValueChanged.forEach((Func) => {
      Func(this._Value);
    });
  }
  public get Value(): number {
    return this._Value;
  }
  public set Active(inner: boolean) {
    this._Active = inner;
    if (inner == false) {
      Laya.Tween.to(
        this.Sprite,
        { alpha: 0 },
        400,
        null,
        Laya.Handler.create(
          this,
          () => {
            // this.Sprite.visible = false;
          },
          null,
          true
        )
      );
    } else {
      // this.Sprite.visible = true;
      Laya.Tween.to(this.Sprite, { alpha: 1 }, 400, null, null, null, true);
    }
  }
  public ReSetToDisable() {
    //this.Sprite.visible = false;
    this.Sprite.alpha = 0;
  }
  public get Active(): boolean {
    return this._Active;
  }
}
