import EventType from "../../../../Tool/EventType";
import adItem from "../../form/adItem";
import UIForm from "../../form/UIForm";

export default class ExportTool_FakeBanner extends UIForm {
  private adList: Laya.List;
  public onAwake() {
    super.onAwake();
    this.adList = this.owner.getChildByName("AdList") as Laya.List;
  }
  public onShow() {
    let adarr = Lite.config.getAd();
    for (let q = 0; q < this.adList.cells.length; q++) {
      if (null == this.adList.cells[q].getComponent(adItem4)) {
        this.adList.cells[q].addComponent(adItem4).init(adarr[q]);
      } else {
        this.adList.cells[q].getComponent(adItem4).init(adarr[q]);
      }
    }
    // this.adList.cells.forEach((inner: Laya.Box) => {
    //   (inner.addComponent(adItem4) as adItem4).init(Lite.config.getRandomAd());
    // });
  }
}
export class adItem4 extends adItem {
  onAdClick() {
    if (null != this.appItem) {
      console.log(`navigatTomini ${this.appItem.title} ${this.appItem.appid}`);
      moosnow.event.sendEventImmediately(EventType.ExportStart);
      moosnow.platform.navigate2Mini(
        this.appItem,
        () => {
          console.log("小游戏跳转成功");
          moosnow.event.sendEventImmediately(EventType.ExportAchive);
        },
        () => {
          console.log("小游戏跳转失败");
        }
      );
    } else {
      console.error("没有跳转数据->appItem:", this.appItem);
    }
  }
}
