import BaseModule from "../BaseModule";

export default class AudioModule extends BaseModule {
    constructor() {
        super();
        this.getSave();
    }
    /**
     * 存储在本地声音有关的设置key（字段字符串）
     * IS_MUTE 是否所有都静音{boolean}
     * IS_MUTE_MUSIC 是否背景音乐静音{boolean}
     * IS_MUTE_SOUND 是否音效静音{boolean}
     * VOLUME_MUSIC 背景音乐音量大小{number}
     * VOLUME_SOUND 音效音量大小{number}
    */
    private IS_MUTE: string = "isMute"
    private IS_MUTE_MUSIC: string = "isMuteMusic"
    private IS_MUTE_SOUND: string = "isMuteSound"
    private VOLUME_MUSIC: string = "volumeMusic"
    private VOLUME_SOUND: string = "volumeSound"
    private _volumeMusic: number = 1
    private _volumeSound: number = 1
    private _isMuteMusic: boolean = false
    private _isMuteSound: boolean = false
    private _isMute: boolean = false
    private _isVib:boolean = true;

    private gameCoinTime: number = 0;
    public onEnable() {
        // this.playGameMusic();
    }
    public playHomeMusci(){
        this.playMusic("subRes/audio/homeBG.mp3");
    }
    public playGameMusic() {
        this.playMusic("subRes/audio/gameBG.mp3");
    }
    public playClickEffect() {
        this.playSound("subRes/audio/btnSound.mp3");
    }
    public playFailEffect() {
        this.playSound("subRes/audio/fail.mp3");
    }
    public playWinEffect() {
        this.playSound("subRes/audio/win.mp3");
    }
    public playDiamonEffect() {
        this.playSound("subRes/audio/diamon.mp3");
    }
    public playHuaXianEffect() {
        this.playSound("subRes/audio/huaxian.mp3");
    }
    public playChongCiEffect() {
        this.playSound("subRes/audio/chongci.mp3");
    }
    public stopChongCiEffect() {
        this.stopSound("subRes/audio/chongci.mp3");
    }
    public playUseMoneyEffect() {
        this.playSound("subRes/audio/useMoney.mp3");
    }


    /**
     * 销毁一个音乐
     * @param 音乐地址
     */
    public destroySound(url: string) {
        Laya.SoundManager.destroySound(url);
    }

    /**
     * 关闭所有音乐
     */
    public stopAll() {
        Laya.SoundManager.stopAll();
    }

    /**
     * 关闭所有音效
     */
    public stopAllSound() {
        Laya.SoundManager.stopAllSound();
    }

    /**
     * 关闭所有背景音效
     */
    public stopMusic() {
        Laya.SoundManager.stopMusic();
    }

    /**
     * 关闭单个音效
     * @param 音乐地址
     */
    public stopSound(url: string) {
        Laya.SoundManager.stopSound(url);
    }

    /**
     * 播放音效
     */
    public playSound(url, loops = 1, complete = null, soundClass = null, startTime = 0) {
        Laya.SoundManager.playSound(url, loops, complete, soundClass, startTime);
    }

    /**
     * 播放背景音乐
     */
    public playMusic(url, loops = 0, complete = null, startTime = 0) {
        Laya.SoundManager.playMusic(url, loops, complete, startTime);
    }

    /**短震动 */
    public vibrateShort(){
        if(!this._isVib) return;
        moosnow.platform.vibrateShort();
    }
    
    /**长震动 */
    public vibrateLong(){
        if(!this._isVib) return;
        moosnow.platform.vibrateLong();
    }

    /**
     * 设置获取背景声音大小
     */
    get volumeMusic() {
        return this._volumeMusic;
    }
    set volumeMusic(value: number) {
        this._volumeMusic = value;
        Laya.SoundManager.setMusicVolume(value);
        this.save();
    }

    /**
     * 设置获取音效声音大小
     */
    get volumeSound() {
        return this._volumeSound;
    }
    set volumeSound(value) {
        this._volumeSound = value;
        Laya.SoundManager.setSoundVolume(value);
        this.save();
    }

    /**
     * 设置获取是否静音背景音乐
     */
    get isMuteMusic() {
        return this._isMuteMusic;
    }
    set isMuteMusic(value) {
        this._isMuteMusic = value;
        Laya.SoundManager.musicMuted = value;
        this.save();
    }

    /**
     * 设置获取是否静音音效音乐
     */
    get isMuteSound() {
        return this._isMuteSound;
    }
    set isMuteSound(value) {
        this._isMuteSound = value;
        Laya.SoundManager.soundMuted = value;
        this.save();
    }

    /**
     * 设置获取是否所有静音
     */
    get isMute() {
        return this._isMute;
    }
    set isMute(value) {
        this._isMute = value;
        Laya.SoundManager.muted = value;
        // this.save();
    }

    /**
     * 设置获取是否震动
     */
    get isVib() {
        return this._isVib;
    }
    set isVib(value) {
        this._isVib = value;
    }

    /**
     * 保存数据到本地
     */
    save() {
        Laya.LocalStorage.setItem(this.IS_MUTE, "" + this.isMute);
        Laya.LocalStorage.setItem(this.IS_MUTE_MUSIC, "" + this.isMuteMusic);
        Laya.LocalStorage.setItem(this.IS_MUTE_SOUND, "" + this.isMuteSound);
        // Laya.LocalStorage.setItem(this.VOLUME_MUSIC, "" + this.volumeMusic);
        // Laya.LocalStorage.setItem(this.VOLUME_SOUND, "" + this.volumeSound);
    }
    getSave() {
        this.isMute = Laya.LocalStorage.getItem(this.IS_MUTE) == "true" ? true : false;
        this.isMuteMusic = Laya.LocalStorage.getItem(this.IS_MUTE_MUSIC) == "true" ? true : false;
        this.isMuteSound = Laya.LocalStorage.getItem(this.IS_MUTE_SOUND) == "true" ? true : false;
        // this.isMute = Laya.LocalStorage.getItem(this.IS_MUTE) == "true" ? true : false;
        // this.isMute = Laya.LocalStorage.getItem(this.IS_MUTE) == "true" ? true : false;
    }

    onDisable() {
    }
}